<!-- Description: -->
<template>
  <div class="actSetConf-container">
    <el-card>
      <el-form ref="actSetConfRef" :model="confData" label-width="150px" :rules="confRules">
        <el-form-item label="" prop="actName">
          <el-input v-model="confData.actName" maxLength='15' placeholder='15'></el-input>
        </el-form-item>
        <el-form-item label="" prop="note">
          <el-input type="textarea" v-model="confData.note" :rows="3" resize="none" maxLength='15' placeholder='15'></el-input>
        </el-form-item>
        <el-form-item label="" prop="idx">
          <el-select v-model="confData.idx" placeholder="" class="select-one">
            <el-option v-for="(val, key) in idxSelect" :key="key" :label="val" :value="key">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="date">
          <el-date-picker v-model="confData.stimeStr" :disabled="timeDisable" value-format="yyyy-MM-dd HH:mm:ss" type="datetime" placeholder=""></el-date-picker>
          
          <el-date-picker v-model="confData.etimeStr" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder=""></el-date-picker>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="banner">
          <el-upload class="avatar-uploader" :before-upload="beforeAvatarUpload" :action="uploadURL" :headers="headerObj" :on-success="upBannerImg" :show-file-list="false">
            <img v-if="confData.banner" :src="confData.banner" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            <div slot="tip" class="el-upload__tip">750*160pngjpg2M</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <quill-editor ref="myTextEditor" v-model="confData.actDesc" :options="editorOption" placeholder="300" @blur="onEditorBlur($event)">
          </quill-editor>
        </el-form-item>
        <el-form-item label="" prop="selectProductList">
          <el-select v-model="confData.selectBrand" multiple collapse-tags placeholder="" @change="getProductList" class="select-two">
            <el-option v-for="item in brandList" :key="item.id" :label="item.name" :value="item.brandCode">
            </el-option>
          </el-select>
          <el-select v-model="confData.selectProductList" multiple collapse-tags placeholder="" @change="restrictSonBrand" class="select-two">
            <el-option v-for="item in productList" :key="item.id" :label="item.allName" :value="item.sn">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="selectCityList">
          <el-select size="small" v-model="confData.selectProvList" multiple collapse-tags filterable placeholder="" @change="getCityList" class="select-three">
            <el-option v-for="item in provList" :key="item.code" :label="item.name" :value="item.code">
            </el-option>
          </el-select>
          <el-select size="small" v-model="confData.selectCityList" multiple collapse-tags filterable placeholder="" @change="getDistrictList" class="select-three">
            <el-option v-for="item in cityList" :key="item.code" :label="item.name" :value="item.code">
            </el-option>
          </el-select>
          <el-select size="small" v-model="confData.selectDistrictList" multiple collapse-tags filterable placeholder="" class="select-three">
            <el-option v-for="item in districtList" :key="item.code" :label="item.name" :value="item.code">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="link">
          <el-input v-model="confData.link" placeholder=''></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="confData.showStatus" :active-value="1" :inactive-value="0"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="confData.status" :active-value="1" :inactive-value="2" :disabled="statusDisabled"></el-switch>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="confirmSubmit"></el-button>
          <el-button @click="$router.push('/market/actMgr')"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import { quillEditor } from 'vue-quill-editor'
export default {
  props: ['form', 'tplCode', 'id', 'clone'],
  components: {
    quillEditor
  },
  data() {
    var validateDate = (rule, value, callback) => {
      if (!this.confData.stimeStr || !this.confData.etimeStr) {
        callback(new Error(''))
      } else if (this.confData.stimeStr >= this.confData.etimeStr){
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateIdx = (rule, value, callback) => {
      if (value == '0') {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateBanner = (rule, value, callback) => {
      if (!this.confData.banner) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var validateDesc = (rule, value, callback) => {
      if (!this.confData.actDesc) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    var valideSelectProductList = (rules, value, callback) => {
      if (this.confData.selectProductList&&this.confData.selectProductList.length>0) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    var valideSelectCityList = (rules, value, callback) => {
      if (this.confData.selectCityList&&this.confData.selectCityList.length>0) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      // 
      editorOption: {
        modules: {
          toolbar: [
            [{ header: 1 }, { header: 2 }],
            [{ list: 'ordered' }, { list: 'bullet' }],
            ['bold', 'italic', 'underline', 'strike', , 'blockquote'],
            [{ color: [] }, { background: [] }, { align: [] }]
          ]
        },
        placeholder: ''
      },
      idxSelect: {},
      confData: {
        id: '', // id
        actCode: '', // 
        actDesc: '', // 
        actName: '', // 
        banner: '', // banner
        form: this.form, // 
        idx: '', // 
        note: '', // 
        stimeStr: '', // 
        etimeStr: '', // 
        showStatus: 0, // 0-1-
        tplCode: '', // 
        extInfo: '', // {link: ''}
        status: 1, // 1-2-
        selectBrand: [], // 
        selectProductList: [], // 
        selectProvList: [], // 
        selectCityList: [], // 
        selectDistrictList: [], // 
        link: '' // 
      },
      statusDisabled: false,
      strategyArr: [], // 
      extInfo: {},
      confRules: {
        actName: [{ required: true, message: '', trigger: 'blur' }],
        note: [{ required: true, message: '', trigger: 'blur' }],
        date: [{ required: true, validator: validateDate, trigger: 'change' }],
        idx: [{ required: true, validator: validateIdx, trigger: 'change' }],
        banner: [{ required: true, validator: validateBanner }],
        desc: [{ required: true, validator: validateDesc }],
        selectProductList: [{ required: true, validator: valideSelectProductList }],
        selectCityList: [{ required: true, validator: valideSelectCityList }],
        link: [{ required: true, message: '', trigger: 'blur' }]
      },
      // actTime: [],
      timeDisable: false,
      uploadURL: '/api/wiseqr/attach/commonAliUpload',
      headerObj: {
        loginId: sessionStorage.getItem('access_loginId') || '2d07e7953a2a63ceda6df5144d1abec3',
        token: sessionStorage.getItem('access_token')
      },
      stepActive:0,

      brandList: [], // 
      productList: [], // 

      provList: [{code: '000000',name: ''}], // 
      cityList: [], // 
      districtList: [], // 
      allProv: [], // [{code:'',name:'',pcode:'',pname:''}, ...]
      allCity: {}, // {'provCode': [{code:'',name:'',pcode:'',pname:''}], ...}
      allDistrict: {}, // {'cityCode': [{code:'',name:'',pcode:'',pname:''}], ...}
      
      initProd: false, // 
      initCity: false, // 
      initDistrict: false // 
    }
  },
  created() {
    if( this.id&&this.id>0 ) {
      // 
      this.initProd = true;
      this.initCity = true;
      this.initDistrict = true;
      this.getDetail();
    } else {
      this.initAjax();
    }
  },
  methods: {
    initAjax() {
      this.getIdxSelect();
      this.getBrandList();
      this.getAllRegions();
    },
    // 
    getIdxSelect() {
      this.$request.post('/api/wiseqr/act/idxSelect', {}, true, res => {
        if (res.ret === '200000') return this.idxSelect = res.data
      })
    },
    // 
    getAllRegions() {
      this.$request.post('/api/wiseqr/dim/allRegions', { withRight:true }, true, res => {
        if (res.ret == '200000') {
          let allAreas = res.data || {}; // {'province':[{code:'',name:'',pcode:'',pname:''}, ...], 'city':{'provCode': [{code:'',name:'',pcode:'',pname:''}], ...}, 'district':{'cityCode': [{code:'',name:'',pcode:'',pname:''}], ...}}
          this.allProv = allAreas['province']||[];
          this.allCity = allAreas['city']||{};
          this.allDistrict = allAreas['district']||{};
          this.provList = this.provList.concat(this.allProv);
          // 
          let strategy = this.strategyArr[0];
          if(strategy) {
            this.confData.selectProvList = strategy.areas.provinceArr||[];
          }
          if (this.initCity) {
            this.getCityList();
          }
        } else {
          this.$message.error(res.messgae);
        }
      })
    },
    getCityList() {
      this.districtList = [];
      this.confData.selectCityList = [];
      this.confData.selectDistrictList = [];
      if(this.confData.selectProvList.length>0) {
        if(this.confData.selectProvList[this.confData.selectProvList.length-1]=='000000') {
          // 
          this.confData.selectProvList = ['000000'];
          this.cityList = [{code: '000000',name: ''}];
          if (this.initCity) {
            this.confData.selectCityList = ['000000'];
            this.initCity = false;
          }
          return;
        } else if(this.confData.selectProvList.length>1&&this.confData.selectProvList[0]=='000000') {
          // 
          this.confData.selectProvList.shift();
        }
        this.cityList = [];
        let tmpList = [];
        for(let i=0; i<this.confData.selectProvList.length; i++) {
          let provCode = this.confData.selectProvList[i];
          tmpList = tmpList.concat(this.allCity[provCode]);
        }
        this.cityList = tmpList;
      } else {
        this.cityList = [];
      }
      if (this.initCity) {
        let strategy = this.strategyArr[0];
        if(strategy) {
          this.confData.selectCityList = strategy.areas.cityArr||[];
        }
        this.initCity = false;
      }
      if (this.initDistrict) {
        this.getDistrictList();
      }
    },
    getDistrictList() {
      this.districtList = [];
      this.confData.selectDistrictList = [];
      if(this.confData.selectProvList.length>0) {
        if(this.confData.selectCityList[this.confData.selectCityList.length-1]=='000000') {
          // 
          this.confData.selectCityList = ['000000'];
          this.districtList = [{code: '000000',name: ''}];
          return;
        }
        this.districtList = [];
        let tmpList = [];
        for(let i=0; i<this.confData.selectCityList.length; i++) {
          let cityCode = this.confData.selectCityList[i];
          tmpList = tmpList.concat(this.allDistrict[cityCode]);
        }
        this.districtList = tmpList;
      } else {
        this.districtList = [];
      }
      if (this.initDistrict) {
        let strategy = this.strategyArr[0];
        if(strategy) {
          this.confData.selectDistrictList = strategy.areas.districtArr||[];
        }
        this.initDistrict = false;
      }
    },
    // 
    getBrandList() {
      this.$request.post('/api/wiseqr/prod/listBrand', {pageSize: '-1'}, true, res => {
        if (res.ret == '200000') {
          this.brandList = res.data.list || [];
          // 
          let strategy = this.strategyArr[0];
          if(strategy) {
            this.confData.selectBrand = strategy.brandArr||[];
            if (this.initProd) {
              this.getProductList();
            }
          }
          return;
        }
        this.$message.error(res.message);
      })
    },
    // 
    getProductList() {
      this.$request.post('/api/wiseqr/prod/list', {brandCodeArr:this.confData.selectBrand, pageSize:'-1'}, true, res => {
        if (res.ret == '200000') {
          this.productList = res.data.list || [];
          if (this.initProd) {
            let strategy = this.strategyArr[0];
            if(strategy) {
              this.confData.selectProductList = strategy.snArr||[];
            }
            this.initProd = false;
          }
          return;
        }
        this.$message.error(res.message);
      });
    },
    copyDetailAttr(act) {
      if(act.status == 1) {
        this.statusDisabled = true;
      }
      // 
      if (act.extInfo) {
        this.extInfo = JSON.parse(act.extInfo);
      }
      this.confData = {
        id: act.id||'', // id
        actCode: act.actCode||'', // 
        actDesc: act.actDesc||'', // 
        actName: act.actName||'', // 
        banner: act.banner||'', // banner
        form: this.form, // 
        idx: act.idx+''||'', // 
        note: act.note||'', // 
        stimeStr: act.stimeStr||'', // 
        etimeStr: act.etimeStr||'', // 
        showStatus: act.showStatus||1, // 0-1-
        tplCode: act.tplCode||'', // 
        extInfo: act.extInfo||'', // {link: ''}
        status: act.status||2, // 1-2-
        selectBrand: [], // 
        selectProductList: [], // 
        selectProvList: [], // 
        selectCityList: [], // 
        selectDistrictList: [], // 
        link: this.extInfo['link']||'' // 
      };
      if (this.clone == '1') {// 
        this.confData.id = '';
        this.confData.actCode = '';
      }
      if (act.stimeStr && act.etimeStr) {
        this.handleDisableTime()
      }
    },
    getDetail() {
      this.$request.post('/api/wiseqr/act/detail', { id: this.id }, true, res => {
        if (res.ret == '200000') {
          this.copyDetailAttr(res.data.act);
          this.strategyArr = res.data.strategyArr;
          this.initAjax();
          return
        }
        this.$message.error(res.messgae)
      })
    },
    handleDisableTime() {
      let newTime = new Date().getTime(),
        stime = new Date(this.confData.stimeStr).getTime()
        if (newTime >= stime) {
          this.timeDisable = true
        }
    },
    // 
    upBannerImg(resule) {
      if (resule.ret === '200000') {
        this.confData.banner = resule.data.accessUrl
        this.$refs.actSetConfRef.validateField('banner', valid => {})
        return
      }
      this.$message.error(resule.message)
    },
    // 
    onEditorBlur() {
      this.$refs.actSetConfRef.validateField('desc', valid => {})
    },
    // form
    confirmSubmit() {
      this.$refs.actSetConfRef.validate(valid => {
        if (valid) {
          this.extInfo = {link: this.confData.link};
          this.confData.extInfo = JSON.stringify(this.extInfo);
          let params = {};
          params.act = this.confData;
          let strategyParams = {tf:{}, tfType:'common', snArr: this.confData.selectProductList, areas: {provinceArr:this.confData.selectProvList, cityArr:this.confData.selectCityList, districtArr:this.confData.selectDistrictList}};
          let strategy = this.strategyArr[0];
          if(strategy) {
            strategyParams.tf['id'] = strategy.tf.id;
            strategyParams.tf['tfCode'] = strategy.tf.tfCode;
          }
          params.strategyArr = [];
          params.strategyArr.push(strategyParams);
          this.$request.post('/api/wiseqr/act/somtfSelf', params, true, res => {
            if (res.ret == '200000') {
              this.$message({type: 'success', message: '!'});
              this.$router.push({path:"/market/actMgr"});
            } else {
              this.$message.error(res.message);
            }
          })
        }
      })
    },
    beforeAvatarUpload(file) {
      const JPGOrPNG = file.type === 'image/jpeg' || file.type === 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!JPGOrPNG) {
        this.$message.error(' JPG  PNG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return JPGOrPNG && isLt2M;
    }
  }
}
</script>
<style lang="scss" scoped>
.actSetConf-container {
  .el-input,
  .el-textarea,
  .select-one {
    width: 600px;
  }
  .select-two {
    width: 297px;
  }
  .select-three {
    width: 197px;
  }
  .el-date-editor {
    width: 289px;
  }
}

.step-style {
	margin-bottom: 40px;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 300px;
  height: 64px;
  line-height: 64px;
  text-align: center;
  &.red-packet {
    width: 100px;
    height: 100px;
  }
}
.avatar {
  width: 300px;
  height: 64px;
  display: block;
  &.red-packet {
     width: 100px;
    height: 100px;
  }
}
.quill-editor {
  width: 600px;
}
.limited-time {
	width:60px;
	margin-left: 10px;
	margin-right: 10px;
	height: 24px;
	border-radius: 4px;
	border:none;
	border:1px solid #ccc;
}
.el-date-editor {
  width: 210px;
}
</style>



